.container {
    max-width: 1280px;
    padding: 0 1.5rem;
    margin: auto;
    overflow: hidden; }
  
  .btn-main, .btn-light, .btn-dark {
    display: inline-block;
    padding: 0.8rem 2rem;
    transition: all 0.5s;
    border: none;
    cursor: pointer; }
  
  .btn-main {
    color: #333;
    background-color: #ffbc00; }
  
  .btn-light {
    color: #333;
    background-color: #f4f4f4; }
  
  .btn-dark {
    color: #f4f4f4;
    background-color: #333; }
  
  button[class^="btn-"]:hover,
  a[class^="btn-"]:hover,
  input[class^="btn-"]:hover {
    background-color: #ffbc00; }
  
  .bg-main {
    background: #ffbc00;
    color: #fff; }
  
  .bg-dark {
    background: #333;
    color: #fff; }
  
  .bg-light {
    background: #f4f4f4;
    color: #000; }
  
  .bg-medium {
    background: #ccc;
    color: #000; }
  
  .lead {
    font-size: 1.3rem;
    margin-bottom: 2rem; }
  
  .text-center {
    text-align: center; }
  
  .py-1 {
    padding: 1rem 0; }
  
  .py-2 {
    padding: 2rem 0; }
  
  .py-3 {
    padding: 3rem 0; }
  
  .py-4 {
    padding: 4rem 0; }
  
  .my-1 {
    margin: 1rem 0; }
  
  .my-2 {
    margin: 2rem 0; }
  
  .my-3 {
    margin: 3rem 0; }
  
  .my-4 {
    margin: 4rem 0; }
  
  .section-title {
    font-size: 2rem;
    display: block;
    padding-bottom: 0.5rem;
    text-align: center;
    font-weight: 100;
    text-transform: uppercase; }
  
  .bottom-line {
    height: 2px;
    width: 3rem;
    background: #ffbc00;
    display: block;
    margin: 0 auto 1rem auto; }
  
  .items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3); }
  
  .item {
    position: relative;
    background: #ffbc00;
    overflow: hidden; }
    .item:after {
      content: "";
      position: absolute;
      display: block;
      background: inherit;
      opacity: 0.9;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transform: scale(2) translateX(-75%) translateY(-75%) rotate(-28deg);
      transition: transform 3s cubic-bezier(0.2, 1, 0.3, 1); }
    .item:hover:after {
      transform: scale(2) translateX(0) translateY(0) rotate(-28deg); }
    .item:hover .item-image {
      transform: scale(1.2); }
    .item:hover .item-text {
      opacity: 1;
      transform: translateY(0); }
    .item-image {
      height: auto;
      transform: translateZ(0);
      display: block;
      transition: transform 750ms cubic-bezier(0.2, 1, 0.3, 1); }
    .item-image::before {
      content: "";
      display: block;
      padding-top: 75%;
      overflow: hidden; }
    .item-image img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: auto;
      line-height: 0; }
    .item-text {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      opacity: 0;
      text-align: center;
      z-index: 1;
      color: #fff;
      transform: translateY(-20%);
      transition: opacity 500ms cubic-bezier(0.2, 1, 0.3, 1), transform 500ms cubic-bezier(0.2, 1, 0.3, 1);
      transition-delay: 300ms; }
    .item-text-wrap {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%); }
    .item-text-title {
      font-size: 2rem;
      padding: 0 1rem;
      margin: 5px 0 0 0; }
    .item-text-category {
      text-transform: uppercase;
      font-size: 1.2rem;
      opacity: 0.7;
      margin: 0; }
  
  * {
    margin: 0;
    padding: 0; }
  
  body {
    font-family: "Dosis", sans-serif;
    line-height: 1.6;
    background: #fff; }
  
  a {
    text-decoration: none;
    color: #333; }
  
  ul {
    list-style: none; }
  
  h2,
  h3,
  h4 {
    text-transform: uppercase; }
  
  img {
    width: 100%; }
  
 
  
    a, a:hover, a:focus, a:active {
      text-decoration: none;
      color: inherit;
 
  }
  #main-footer {
    position: relative;
    margin-top: 50px;
    background: #333;
    color: #fff;
    height: 5rem; 
    }
    #main-footer .footer-content {
      display: flex;
      justify-content: space-between;
      height: 5rem;
      align-items: center; }
      footer .footer-content {
        display: flex;
        justify-content: space-between;
        height: 5rem;
        align-items: center; }
        footer .footer-content .social a .fab {
          margin-right: 1rem;
          border: 2px #fff solid;
          border-radius: 50%;
          height: 20px;
          width: 20px;
          line-height: 20px;
          text-align: center;
          padding: 0.5rem; }
          footer .footer-content .social .fab:hover {
            background: #ffbc00; }
      
  
 
  
  @media (max-width: 500px) {
   
    #main-footer {
      height: 7rem; }
      #main-footer .footer-content {
        flex-direction: column;
        padding: 1rem;
        height: 5rem; } }
  
 
  